.all_wrapper {
    width: 100%;
}

.head_text {
    font-family: 'Ruda', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 50px;
    color: var(--color-additional);
    letter-spacing: 0.015em;
    margin: 210px 0 85px 85px;
}
.lineHead {
    text-decoration-line: overline;
    text-decoration-style: solid;
    text-decoration-color: var(--color-additional);
}

.wrapper {
    height: 750px;
    width: 1265px;
    background-color: var(--color);
    display: flex;
    flex-direction: row;
    margin: 85px auto 260px;
}

.card {
    height: 615px;
    width: 316px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card_down {
    margin-top: 195px;
}

.card__name{
    font-family: 'Ruda', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    color: var(--color-additional);
    margin-bottom: 3px;
}

.card__post {
    font-family: 'Ruda', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.025em;
    color: var(--light-gray);
    margin-bottom: 20px;

}

.card__photo {
    width: 316px;
    height: 397px;
    /* background: rgba(90, 90, 90); */
    z-index: 3;
}
/*карточки ребят*/
.ant {
    background: url('../../images/anton_photo11.jpg');
}
.ant:hover {
    background: url('../../images/anton_photo1.jpg');
    transition: 0.5s;
}
.nail {
    background: url('../../images/nail_photo.jpg');
}
.nail:hover {
    background: url('../../images/nail_photo1.jpg');
    transition: 0.5s;
}
.rus {
    background: url('../../images/ruslan_photo.jpg');
}
.rus:hover {
    background: url('../../images/ruslan_photo1.jpg');
    transition: 0.5s;
}

.son {
    background: url('../../images/sonya_photo.jpg');
}
.son:hover{
    background: url('../../images/sonya_photo1.jpg');
    transition: 0.5s;
}


.card__block {
    width: 316px;
    height: 145px;
    background-color: var(--color-additional);
    display: flex;
    justify-content: flex-end;
    transition: .5s;
    z-index: 2;
    position: absolute;
    bottom: 0;
}

.arr_down, .arr_top{
    width: 28px;
    height: 28px;
    margin: 30px 25px 0 0;
}

.hidden {
    bottom: 145px;   
}

.card__info_block {
    z-index: 1;
    width: 316px;
    height: 145px;
    background-color: var(--light-gray);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.card_info {
    font-family: 'Ruda', sans-serif;
    font-size: 15px;
    font-weight: 600;
    margin: 20px;
    color: #111;
}

.social {
    width: 8vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    gap: 30px;
    align-items: center;
    margin-bottom: 15px;
}
.weight {
    font-weight: 800;
}

/* Анимации для стрелочек */

@keyframes swing {
    30% {
        transform: translateY(5px);
    }

    50% {
        transform: translateY(-2px);
    }

    65% {
        transform: translateY(2px);
    }

    80% {
        transform: translateY(-1px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes swing_reverse {
    30% {
        transform: translateY(-5px);
    }

    50% {
        transform: translateY(2px);
    }

    65% {
        transform: translateY(-2px);
    }

    80% {
        transform: translateY(1px);
    }

    100% {
        transform: translateY(0);
    }
}

.arr_down {
    animation: swing 2s ease;
    animation-iteration-count: infinite;
}

.arr_top {
    animation: swing_reverse 2s ease;
    animation-iteration-count: infinite;
}

@media (max-width: 1250px) {
    .wrapper {
        flex-direction: column;
        width: 100%;
        height: 100%;
        align-items: center;
    }

    .all_wrapper {
        margin-bottom: 10px;
    }

    .card_down {
        margin-top: 10px;
    }
}
